      width: 100%;
      height: 0px;
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.3s ease-in-out;
      z-index: 2; }
    .tabs-section .tabslink:hover:before {
      height: 100%;
      z-index: 2;
      bottom: 0; }
    .tabs-section .tabslink:hover p {
      color: #fff;
      opacity: 1; }
      .tabs-section .tabslink:hover p:before {
        opacity: 0.1;
        font-size: 40px;
        top: -80%; }
    .tabs-section .tabslink p {
      opacity: 0.6;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 2;
      transition: 0.5s ease;
      padding: 0;
      margin: 0;
      color: #686868;
      backface-visibility: hidden;
      font-weight: 400; }
      .tabs-section .tabslink p:before {
        content: attr(data-title);
        position: absolute;
        height: auto;
        width: auto;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: 0.1;
        font-size: 40px;
        transition: 1s ease-out;
        z-index: -1;
        font-weight: 600;
        top: 110%; }
    .tabs-section .tabslink.active {
      background-color: white;
      z-index: 0;
      border-right: 0px;
      border-left: 0px;
      height: 110px;
      bottom: 0px;
      overflow: hidden; }
      .tabs-section .tabslink.active:before {
        content: "";
        width: 100%;
        height: 5px;
        top: 0;
        left: 0; }
      .tabs-section .tabslink.active p {
        opacity: 1;
        background: -webkit-linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent; }
  .tabs-section .holder-content {
    background-color: white;
    margin-top: 0px;
    z-index: -3;
    position: relative;
    opacity: 1;
    padding: 40px 60px;
    overflow: hidden;
    transition: all 1s ease; }
    .tabs-section .holder-content:after {
      content: "";
      height: 5px;
      width: 100%;
      position: absolute;
      background-image: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      left: 0;
      bottom: 0;
      z-index: -2;
      transition: all 1s ease; }
    .tabs-section .holder-content .tabcontent {
      display: none;
      min-height: 180px; }
      .tabs-section .holder-content .tabcontent.active {
        transition: all 1s ease;
        display: block; }
      .tabs-section .holder-content .tabcontent h3 {
        font-size: 40px;
        top: 75px;
        transform: rotate(90deg);
        position: absolute;
        left: -90px;
        opacity: 0.1;
        width: 200px;
        height: 60px;
        background: -webkit-linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        padding: 0;
        animation: city 1s ease; }
      .tabs-section .holder-content .tabcontent p {
        color: #686868;
        margin: 0;
        padding: 0;
        line-height: 28px;
        font-weight: 100;
        transition: all 1s ease;
        animation: fadeEffect 0.6s ease;
        width: 100%;
        font-size: 16px; }

@keyframes fadeEffect {
  from {
    opacity: 0;
    margin-left: 30px; }
  to {
    opacity: 1;
    margin-left: 0; } }

@keyframes city {
  from {
    left: -150px; }
  to {
    left: -90px; } }

@keyframes tabEffect {
  from {
    top: -40px; }
  to {
    top: 0px; } }

.modal {
